export default {
	data() {
		return {
			showmodule:true,
			useragetype:0,
			level: [
				{imgurl:require('../../static/images/three.png'),level:3},
				{imgurl:require('../../static/images/four.png'),level:4},
				{imgurl:require('../../static/images/five.png'),level:5},
				{imgurl:require('../../static/images/six.png'),level:6},
				{imgurl:require('../../static/images/seven.png'),level:7},
				{imgurl:require('../../static/images/eight.png'),level:8}
			],
			ages: [{
					ageType: 0,
					imgurl:require('../../static/images/age1.png')
				},
				{
					ageType: 1,
					imgurl:require('../../static/images/age2.png')
				},
				{
					ageType: 2,
