				// },
			]
		}
	},
	methods: {
		// 开始游戏
		play(num) {
			uni.navigateTo({
				url: '../play/play?num=' + num +'&userAgeType='+this.useragetype
			});
		},
		// 选择年龄
		select_age: function(evt) {
